import React from "react";
import { Bar } from "react-chartjs-2"; // Bar  
import { Chart as ChartJS, CategoryScale, LinearScale, BarElement, Title, Tooltip, Legend } from 'chart.js';

// Chart.js   
ChartJS.register(
  CategoryScale,
  LinearScale,
  BarElement,
  Title,
  Tooltip,
  Legend
);

const Graph = ({ data, onClose }) => {
  if (!data || data.length === 0) {
    return <div> </div>;
  }

  //     
  const sortedData = data.sort((a, b) => {
    return new Date(a.month) - new Date(b.month); //  
  });

  //   
  const chartData = {
    labels: sortedData.map((item) => item.month), // x: 
    datasets: [
      {
        label: " ",
        data: sortedData.map((item) => item.profit), // y: 
        backgroundColor: "#4caf50", //  
        borderColor: "#388e3c", //   
        borderWidth: 1, //   
      },
    ],
  };

  //   
  const chartOptions = {
    responsive: true,
    maintainAspectRatio: false, //     
    aspectRatio: 1.5, //       
    plugins: {
      tooltip: {
        callbacks: {
          label: (tooltipItem) => {
            return `: ${tooltipItem.raw.toLocaleString()} `; //   
          },
        },
      },
    },
    scales: {
      y: {
        ticks: {
          stepSize: 2000, //   2000 
          callback: (value) => value.toLocaleString(), // y  
        },
      },
    },
  };

  return (
    <div className="modal-container2">
      <div className="modal-content2">
        <div className="modal-header2">
          <div className="modal-title2">  </div>
        </div>
        <div className="chart-container">
          <Bar data={chartData} options={chartOptions} /> {/* Bar   */}
        </div>
        <button onClick={onClose} className="close-btn"></button>
      </div>
    </div>
  );
};

export default Graph;
